.container {
  width: 90%;
  max-width: 1366px;
  margin-left: auto;
  margin-right: auto; }

*, *::before, *::after {
  box-sizing: border-box; }

html {
  font-size: 14px; }

body {
  font-family: 'DIN Pro', sans-serif; }

.header {
  position: relative;
  background-color: #f8f6f7; }
  .header__navbar {
    display: flex;
    align-items: center; }
    .header__navbar--small {
      height: 3.214rem; }
      .header__navbar--small::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 1px;
        background-color: #d7d7d7;
        top: 3.214rem;
        left: 0; }

.logo {
  display: flex;
  text-decoration: none;
  align-items: center; }
  .logo__title {
    margin-left: 1.071rem;
    color: #202020;
    font-size: 1.714rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.154rem; }
  .logo__subtitle {
    display: block;
    font-size: 0.857rem;
    font-weight: 400;
    letter-spacing: 0.008rem;
    text-transform: none; }
  .logo--small {
    margin-right: 2.142rem; }

.menu {
  display: flex;
  list-style: none;
  padding: 0; }

.item {
  display: flex;
  align-items: center; }
  .item--small:not(:last-child)::after {
    content: '|';
    color: #d0d0d0;
    margin-left: 1.428rem;
    margin-right: 1.428rem; }
  .item--main {
    margin-right: 2.5rem; }
    .item--main:last-of-type {
      margin-right: 0; }
  .item__link {
    display: flex;
    align-items: center;
    color: #d0d0d0; }
    .item__link--main {
      color: #202020;
      font-size: 1.142rem;
      line-height: 2.142rem;
      font-family: "DIN Pro - Medium";
      font-weight: 500;
      letter-spacing: 0.011rem; }

.link {
  text-decoration: none; }
  .link__img {
    margin-left: 0.5rem;
    height: 0.857rem;
    fill: #d0d0d0; }
    .link__img--last {
      height: 1.571rem;
      margin-left: 0;
      fill: #919191; }

.log-in {
  display: flex;
  align-items: center;
  margin-left: auto;
  border: none;
  background-color: transparent;
  color: #202020; }
  .log-in__img {
    height: 0.857rem;
    margin-left: 0.642rem;
    fill: #d0d0d0; }

.navigation {
  margin-left: auto; }

.search__img {
  fill: #f47920;
  height: 1rem; }

.arrow-down {
  margin-left: 0.357rem;
  height: 0.857rem; }

.favorites {
  width: 4.357rem;
  height: 6rem;
  margin-left: 3.571rem;
  margin-right: 0.714rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../img/header/flag-empty.svg") no-repeat center/cover; }
  .favorites__img {
    height: 1.5rem;
    fill: #f47920; }
  .favorites__value {
    color: #f47920;
    font-size: 1.142rem;
    line-height: 2.142rem;
    letter-spacing: 0.011rem; }

.bag {
  width: 4.357rem;
  height: 6rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../img/header/flag.svg") no-repeat center/cover; }
  .bag__img {
    height: 1.5rem;
    fill: #ffffff; }
  .bag__value {
    color: #ffffff;
    font-size: 1.142rem;
    line-height: 2.142rem;
    letter-spacing: 0.011rem; }
